body {
  background-color:white
}
.header {

    background-color: #f3f3f4;
    width: 93%;
    padding-bottom: 10px;
    padding: 0px;
    padding-right: 50px;
    margin-left: 20px;


}

.row1 ul {
  width: 83%;
  display: flex;
  flex-direction: row;
  padding: 5px ;
  justify-content: space-between;
  align-items: center;
  justify-items: space-between;
  padding-left: 40px;
  padding-right: 30px;
}

.row1 ul li {
  padding-top: 5px;
  padding-left: 60px;
  padding-right: 25px;
  list-style-type: none;
}
.list_whitecircle {
  min-width: 20px;
  max-width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  min-height: 20px;
  max-height: 20px;
  border-radius: 30px;
  color: #c3cad9;
  font-size: 1.2rem;
  border: 1px solid lightgray;
  
}
.Innerbox {
  margin-top: 10px;
