body{
    margin: 0;
}
.titlebar_top{
    background-color: #f3f3f4;
    padding: 15px 30px;
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 0px 0px 1px lightgray;
 
}
.titlebar_bagSquare{
    background-color: white;
    width: 16%;
    padding: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.titlebar_top_col1{
    width: 11%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.titlebar_top_col1 label,
.titlebar_top_col21 label{
    font-size: 14px;
    color: #7C8AA5;
}
.titlebar_top_col2{
    width: 20%;
    display: flex;
    justify-content: space-around;
}
.titlebar_top_col21{
    background-color: white;
    width: 26%;
    display: flex;
    align-items: center;
    padding: 7px 30px;
    border-radius: 20px;
    border: 1px solid #7C8AA5;
}
.titlebar_top_col22{
    background-color: #8833FF;
    width: 58%;
    display: flex;
    align-items: center;
    padding: 8px 30px;
    border-radius: 20px;
}
.titlebar_top_col21 label,
.titlebar_top_col22 label
{
    font-size: 14px;
    padding-left: 10px;
}
.titlebar_top_col22 label{
    color: #e5e5e5;
}
.titlebar_bagIcon,
.titlebar_plusIcon{
    color: #C3CAD9;
}
.titlebar_downloadIcon{
    color: rgb(113, 183, 183);
    font-weight: 100;
}
.titlebar_bagIcon_Products{
    margin-left: 38px;
    margin-right: 20px;
    font-size: 20px;

}
.titlebar_bagIcon_Active{
    margin-left: 215px;
    margin-right: 20px;
    color: #8833FF;
}
.titlebar_bagIcon_Draft{
    margin-left: 20px;
    margin-right: 20px;
}
.titlebar_bagIcon_Assembly{
    margin-left: 20px;
    margin-right: 20px;
}
.titlebar_underline{
    
    background-color: #8833FF;
    margin: 0px 0px 0px 390px;
    width: 74px;
    font-size: 2px;
    
}